.listHeader{@apply mb-[30px] flex max-sm:mb-[10px]}.listHeader .box{@apply flex grow gap-[50px] max-md:gap-[20px]}.listHeader .box .label{@apply w-[40%] text-[16px] font-medium leading-[19px] text-[#6A6A6A] max-sm:text-[14px]}.listHeader .box .label:where(:global(.dark),:global(.dark) *){@apply text-[#9B9B9B]}.listBody{@apply overflow-auto pb-[130px] pr-[15px] max-sm:pb-0 max-sm:pr-0;height:calc(100% - 48px)}@media not all and (min-width: 640px){.listBody{height:calc(100% - 29px)}}