.button{@apply flex w-full items-center justify-center rounded-[8px] text-[16px] font-medium transition-colors ease-linear max-sm:text-[15px]}.button .loader{@apply w-[20px] text-black}.button .loader .icon{@apply h-full w-full animate-spin}.button .loader:where(:global(.dark),:global(.dark) *){@apply text-white}.hFull_on{@apply h-full}.hFull_off{@apply h-[43px] max-sm:h-[40px]}.uninitialized{@apply bg-button-main}.uninitialized:hover{@apply bg-button-hover}.uninitialized:active{@apply bg-button-click}.loading{@apply bg-button-disabled-light}.loading:where(:global(.dark),:global(.dark) *){@apply bg-button-disabled-dark}.success{@apply bg-button-success}.error{@apply bg-button-failure}.disabled_on{@apply bg-[#E6EBFA] text-[#A7B9CA]}.disabled_on:where(:global(.dark),:global(.dark) *){@apply bg-[#36373B] text-[#7A7A7A]}.disabled_off{@apply text-white}.errorMessage{@apply mt-[5px] text-center text-[14px] text-button-failure}