.inputBox{@apply mb-[5px] flex h-[32px] items-center rounded bg-[#e6f7ff] transition-shadow}.inputBox .input{@apply w-full bg-[#e6f7ff] pl-[12px] text-[14px] font-medium text-[#16171a] outline-none}.inputBox:focus{@apply shadow-[0_0_0_2px_rgba(24,144,255,.2)]}.listValues{@apply flex flex-wrap gap-[10px]}.listValues .value{@apply flex items-center gap-[5px] rounded border bg-[#e6f7ff] px-[5px] text-[#46a9d7]}