.label{@apply mb-[5px] text-[14px] opacity-80}.inputContainer{@apply flex h-[32px] cursor-pointer items-center rounded bg-[#e6f7ff] transition-shadow}.inputContainer .input{@apply w-full bg-[#e6f7ff] pl-[12px] text-[14px] font-medium text-[#16171a] outline-none}.inputContainer:focus{@apply shadow-[0_0_0_2px_rgba(24,144,255,.2)]}.listValues{@apply absolute z-[100] w-full bg-[#ecf8fd]}.errorMessage{@apply text-[12px] text-red-400}