.page{@apply flex h-full items-center justify-center p-[30px]}.page .limit{@apply flex w-[388px] flex-col}.page .limit .title{@apply mb-[20px] text-center text-[25px] font-bold leading-[30px] text-theme-text max-sm:mb-[15px] max-sm:text-[20px]}.page .limit .title:where(:global(.dark),:global(.dark) *){@apply text-themeDark-text}.page .limit .message{@apply mb-[35px] text-center text-[15px] text-[#838383] max-sm:mb-[25px] max-sm:text-[14px]}.page .limit .backLink{@apply flex w-fit items-center justify-center gap-[5px] text-[15px]}.page .limit .backLink .link{@apply text-theme-blue}