.container{@apply h-[100vh] p-[25px] max-sm:px-[10px] max-sm:py-[20px]}.container .limit{@apply relative
     flex
     h-[100%]
     min-h-[500px]
     w-[100%]
     flex-col
     justify-start
     rounded-xl
     bg-white}.container .limit .logo{@apply absolute left-0 right-0 top-[55px] mx-auto flex items-center justify-center gap-[15px] max-sm:gap-[11px]}.container .limit .logo .imageLimit{@apply h-[39px] w-[50px] max-sm:h-[29px] max-sm:w-[37px]}.container .limit .logo .label{@apply text-[40px] font-bold leading-[47px] text-[#0A8CF7] max-sm:text-[29px] max-sm:leading-[34px]}.container:where(:global(.dark),:global(.dark) *) .limit{@apply bg-[#2C2D31]}