.sidebar{@apply flex h-fit min-h-[400px] w-full flex-col rounded-[20px] bg-white max-sm:mb-[20px] max-sm:min-h-fit;grid-area:menuGrops}.sidebar .label{@apply w-fit bg-[#B7D4FF] px-[25px] py-[12px] font-medium leading-[21px] text-[#4674B8] max-sm:hidden;border-radius:20px 0px}@media not all and (min-width: 768px){.sidebar .label{border-radius:20px 20px 0 0}}.sidebar .label:where(:global(.dark),:global(.dark) *){@apply bg-[#585E67] text-[#C5C5C5]}.sidebar .mobileLabel{@apply hidden cursor-pointer items-center justify-between p-[15px] text-[15px] font-medium text-[#4674B8] max-sm:flex}.sidebar .groupsBox{@apply flex grow flex-col justify-between px-[25px] pb-[30px] pt-[18px] max-md:px-[10px] max-sm:px-[15px] max-sm:pb-[20px]}.sidebar .groupsBox .menu{@apply flex flex-col gap-[8px] overflow-auto max-sm:mb-[23px]}.sidebar .groupsBox .menu>div{@apply flex flex-col gap-[8px]}.sidebar .groupsBox .menu>div .group{@apply flex
            items-center
            justify-between
            rounded-[6px]
            text-[16px]
            font-medium
            leading-[19px]
            text-[#171717]
            transition-colors
            ease-linear
            max-sm:text-[14px]
            max-sm:leading-[16px]}.sidebar .groupsBox .menu>div .group_active{@apply bg-[#4671B0] text-[#FFFFFF]}.sidebar .groupsBox .menu>div .group .link{@apply block grow truncate py-[7px] pl-[15px]}.sidebar .groupsBox .menu>div .group .actions{@apply flex gap-[9px] py-[7px] pr-[15px]}.sidebar .groupsBox .menu>div .group:not(.sidebar .groupsBox .menu>div .group_active):hover{@apply bg-[#F0F5FC]}.sidebar .groupsBox .menu>div .group:not(.sidebar .groupsBox .menu>div .group_active):hover:where(:global(.dark),:global(.dark) *){@apply bg-[#36373B]}.sidebar .groupsBox .menu>div .group:not(.sidebar .groupsBox .menu>div .group_active):where(:global(.dark),:global(.dark) *){@apply text-[#A3ABB6]}.sidebar .groupsBox .menu>div .divider{@apply h-[1px] w-full bg-[#EFEFF9]}.sidebar .groupsBox .menu>div .divider:where(:global(.dark),:global(.dark) *){@apply bg-[#242527]}.sidebar .groupsBox .menu>div:last-child .divider{@apply hidden}.sidebar .groupsBox .button{@apply h-[43px] w-full}.sidebar .groupsBox_close{@apply max-sm:h-0 max-sm:overflow-hidden max-sm:p-0}.sidebar:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}