.buttonLogout{@apply flex w-full items-center justify-center rounded-[6px] border border-[#4674B8] py-[10px] transition-colors ease-linear}.buttonLogout span{@apply text-[14px] font-medium leading-[16px] text-[#4674B8] transition-colors ease-linear max-sm:text-[13px] max-sm:leading-[15px]}.buttonLogout_uninitialized:hover{@apply border-[#6491D3] bg-[#6491D3]}.buttonLogout_uninitialized:hover span{@apply text-[#FFFFFF]}.buttonLogout_uninitialized:active{@apply border-[#4671B0] bg-[#4671B0]}.buttonLogout_uninitialized:active span{@apply text-[#FFFFFF]}.buttonLogout_loaded{@apply border-[#E6EBFA]}.buttonLogout_loaded:where(:global(.dark),:global(.dark) *){@apply border-[#36373B]}.buttonLogout_error{@apply border-[#D03A3A] bg-[#D03A3A]}.buttonLogout_error span{@apply text-[#FFFFFF]}