.button{@apply flex h-[24px] w-[24px] items-center justify-center rounded-[5px] bg-[#EFEFEF] transition-colors ease-linear max-sm:h-[20px] max-sm:w-[20px]}.button .icon{@apply stroke-[#B1B1B1] transition-colors ease-linear}.button_add .icon{@apply h-[14px] w-[14px] max-sm:h-[11px] max-sm:w-[11px]}.button_add:hover{@apply bg-[#D8FFD1]}.button_add:hover .icon{@apply stroke-[#3EAA2D]}.button_delete .icon{@apply h-[14px] w-[13px] max-sm:h-[11px] max-sm:w-[10px]}.button_delete:hover{@apply bg-[#FFD8D8]}.button_delete:hover .icon{@apply stroke-[#D03A3A]}.button_edit .icon{@apply h-[14px] w-[14px] max-sm:h-[10px] max-sm:w-[10px]}.button_edit:hover{@apply bg-[#ACC9F4]}.button_edit:hover .icon{@apply stroke-[#4671B0]}.button:active{@apply bg-[#9CC4FF]}.button:active .icon{@apply stroke-[#FFFFFF]}.button:where(:global(.dark),:global(.dark) *){@apply bg-[#474747]}.button:where(:global(.dark),:global(.dark) *) .icon{@apply stroke-[#B1B1B1]}