.table{@apply w-full border-separate;border-spacing:0 10px}.table .thead{@apply max-md:hidden}.table .thead .row{@apply text-[16px] font-medium text-[#8B8B8D]}.table .thead .row .col:first-child{@apply pb-[10px] pl-[60px] text-left font-medium max-md:pl-[40px]}.table .thead .row .col:last-child{@apply mb-[10px] text-left font-medium}