.profile{@apply flex items-end justify-between}.profile .info .avatar{@apply h-[40px] w-[40px] rounded-full bg-[#f0f0f0] p-[10px]}.profile .info .role{@apply mb-[2px] ml-auto w-fit bg-[#4674B8] px-[9px] py-[2px] text-[9px] font-bold capitalize leading-[11px] text-[#EEEEF8];border-radius:0px 5px}.profile .info .username{@apply max-w-[100px] overflow-hidden truncate text-[14px] font-medium capitalize leading-[16px] text-[#171717]}.profile .info .username:where(:global(.dark),:global(.dark) *){@apply text-[#C4C4C4]}.profile .info .box{@apply relative flex items-center gap-[15px] pr-[25px]}.profile .info .box::before{content:"";@apply absolute right-0 top-1/2 h-[20px] w-[1px] -translate-y-1/2 bg-[#EEEEF8]}.profile .info .box::before:where(:global(.dark),:global(.dark) *){@apply bg-[#36373B]}.profile .button{@apply w-[80px]}