.table{@apply w-full border-separate;border-spacing:0 10px}.table .colgroup{@apply max-md:hidden}.table .thead{@apply max-md:hidden}.table .thead .row{@apply text-[16px] font-medium text-[#8B8B8D]}.table .thead .row .col{@apply px-[10px] pb-[10px] text-center font-medium}.table .thead .row .col:first-child{@apply pb-[10px] pl-[60px] text-left max-md:pl-[40px]}.table .thead .row .col:last-child{@apply text-left}.empty{@apply mb-[20px] flex h-[130px] w-full items-center justify-center rounded-[10px] border}.empty .message{@apply text-[18px] text-[#8B8B8D]}.empty:where(:global(.dark),:global(.dark) *){@apply border-[#6A6A6A]}