.container{@apply flex h-[100vh] gap-[20px] p-[25px] max-sm:p-[12px]}.container .imageBox{@apply flex w-[50%] flex-col items-center justify-between rounded-[20px] bg-cover bg-center pb-[30px] pt-[37px] max-sm:hidden;background-image:linear-gradient(0deg, rgba(70, 116, 184, 0.7) 0%, rgba(70, 116, 184, 0) 40%),url("../../../../../public/images/login.webp")}.container .imageBox .logo{@apply flex items-center justify-center gap-[15px]}.container .imageBox .logo .imageLimit{@apply h-[39px] w-[50px]}.container .imageBox .logo .imageLimit .image{@apply w-full}.container .imageBox .logo .label{@apply text-[40px] font-bold text-[#0A8CF7]}.container .imageBox .copy{@apply text-[15px] font-normal tracking-[1px] text-white}.container .imageBox:where(:global(.dark),:global(.dark) *){background-image:linear-gradient(180deg, rgba(22, 35, 55, 0) 67%, #162337 109%),url("../../../../../public/images/login.webp")}.container .contentBox{@apply w-[50%] rounded-[20px] bg-backdrop-secondary-light max-sm:w-[100%]}.container .contentBox:where(:global(.dark),:global(.dark) *){@apply bg-backdrop-secondary-dark}