.page{@apply flex h-full items-center justify-center p-[30px] max-sm:p-[28px]}.page .limit{@apply flex w-[388px] flex-col}.page .limit .title{@apply mb-[40px]
            text-center
            text-[25px]
            font-semibold
            leading-[30px]
            text-theme-text
            max-sm:mb-[15px]
            max-sm:text-[20px]
            max-sm:leading-[23px]}.page .limit .title:where(:global(.dark),:global(.dark) *){@apply text-themeDark-text}.page .limit .logo{@apply mb-[25px] hidden items-center justify-center gap-[15px] max-sm:flex}.page .limit .logo .imageLimit{@apply h-[39px] w-[50px]}.page .limit .logo .imageLimit .image{@apply w-full}.page .limit .logo .label{@apply text-[40px] font-bold leading-[47px] text-[#0A8CF7]}.page .limit .registerLink{@apply flex justify-center gap-[10px] text-[15px] font-normal}.page .limit .registerLink .label{@apply text-theme-gray}.page .limit .registerLink .label:where(:global(.dark),:global(.dark) *){@apply text-themeDark-gray}.page .limit .registerLink .link{@apply text-theme-blue}