.container{@apply w-full}.container .label{@apply mb-[6px] text-[13px] text-[#828282]}.container .textField{@apply relative flex w-full items-center rounded bg-[#e6f7ff]}.container .textField .input{@apply w-full resize-y bg-[#e6f7ff] px-[15px] py-[5px] text-[14px] font-medium text-[#16171a] outline-none}.container .textField .clearButton{@apply absolute right-[0px] top-[0px] flex items-center gap-[3px] p-[5px] text-center}.container .textField .clearButton .icon{@apply w-[14px] cursor-pointer opacity-60 transition-opacity ease-linear}.container .textField .clearButton .icon:hover{@apply opacity-100}.container .errorText{@apply mt-[5px] text-[13px] font-normal text-input-failure}