.label{@apply mb-[6px] text-[13px] text-input-label}.container{@apply relative flex items-center}.container .input{@apply h-[40px] w-full rounded-[8px] border-[1px] bg-transparent px-[30px] py-[11px] text-[15px] font-normal outline-none placeholder:text-[#B1B1B1] max-sm:h-[35px]}.container .input:focus~.clearButton{@apply text-input-main}.container .input:where(:global(.dark),:global(.dark) *){@apply placeholder:text-[#6A6A6A]}.container .clearButton{@apply absolute right-0 flex h-full items-center gap-[3px] pr-[12px] text-center text-input-idle}.container .clearButton .icon{@apply w-[14px] cursor-pointer opacity-60 transition-opacity ease-linear}.container .clearButton .icon:hover{@apply opacity-100}.container .clearButton:where(:global(.dark),:global(.dark) *){@apply text-input-idleDark}.error_on .input{@apply border-input-failure text-input-failure}.error_on .clearButton .icon{@apply text-input-failure}.error_off .input{@apply border-input-idle text-input-main focus:border-input-main}.error_off .input:where(:global(.dark),:global(.dark) *){@apply border-input-idleDark}.error_off .clearButton .icon{@apply text-inherit}.errorMessage{@apply mt-[5px] text-[13px] font-normal leading-[15px] text-input-failure}