.page{@apply relative flex justify-between gap-[20px] max-sm:flex-col;height:calc(100% - 43px - 35px - 25px)}.page .container{@apply h-full w-[50%] rounded-[20px] bg-[#FFFFFF] max-sm:w-[100%]}.page .container:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}@media not all and (min-width: 640px){.page{height:100%}}