.book{@apply flex items-center border-t border-[#EFEFF9] py-[16px] transition-colors ease-linear}.book .box{@apply flex grow items-center gap-[50px] max-md:gap-[20px]}.book .box .value{@apply block h-full w-[40%] truncate text-[16px] font-normal text-[#6A6A6A] max-sm:text-[13px];max-width:clamp(6.688rem,-1.45rem + 18.6vw,12.5rem)}@media not all and (min-width: 640px){.book .box .value{max-width:clamp(6.688rem,-2.688rem + 40vw,13.313rem)}}.book:where(:global(.dark),:global(.dark) *){@apply border-[#242527]}