.burger .showButton{@apply flex h-[35px] w-[35px] cursor-pointer items-center justify-center rounded-[6px] bg-white;box-shadow:0px -1px 10px 0px rgba(70,116,184,.12)}.burger .showButton:where(:global(.dark),:global(.dark) *){@apply bg-[#575759];box-shadow:0px -1px 10px 0px rgba(70,116,184,.12)}.burger .hideButton{@apply relative z-[91] hidden h-[35px] w-[35px] items-center justify-center rounded-[6px] bg-[#4674B8]}.burger .backdrop{@apply absolute left-0 top-0 z-[90] h-full w-full transition-all;visibility:hidden;background-color:rgba(183,212,255,.1019607843);opacity:0;backdrop-filter:blur(2.5px)}.burger .backdrop:where(:global(.dark),:global(.dark) *){background-color:rgba(0,0,0,.1019607843)}.burger .backdrop .menuModal{@apply ml-[15px] mr-[25px] mt-[35px] hidden rounded-[10px] bg-white px-[20px] pb-[35px] pt-[53px]}.burger .backdrop .menuModal:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}.burger_visible .showButton{@apply hidden}.burger_visible .hideButton{@apply flex}.burger_visible .backdrop{visibility:visible;opacity:1}.burger_visible .backdrop .menuModal{@apply block}