.container{@apply flex max-sm:flex-col}.container .limit{@apply p-[25px] pr-[20px] max-sm:px-[10px] max-sm:py-[20px]}.container .limit .navigate{@apply sticky top-[25px] z-[100] flex w-[80px] flex-col items-center justify-between rounded-[20px] bg-[#FFFFFF] py-[30px];height:calc(100vh - 50px)}.container .limit .navigate .section{@apply flex flex-col items-center gap-[20px]}.container .limit .navigate .section .logo{@apply h-[39px] w-[50px]}.container .limit .navigate .section .liksBox{@apply flex flex-col gap-[10px]}.container .limit .navigate:where(:global(.dark),:global(.dark) *){@apply bg-[#2C2D31]}.container .limit .navigateMobile{@apply hidden items-center justify-between}.container .limit .navigateMobile .logo{@apply h-[35px] w-[45px]}.container .limit .navigateMobile .currentPage{@apply truncate text-[20px] font-medium leading-[23px] text-[#171717];max-width:clamp(9.375rem,-3.892rem + 56.6vw,18.75rem)}.container .limit .navigateMobile .currentPage:where(:global(.dark),:global(.dark) *){@apply text-[#F0F5FC]}.container .limit .backdrop{@apply absolute left-0 top-0 z-[90] h-full w-full transition-all;visibility:hidden;background-color:rgba(183,212,255,.1019607843);opacity:0;backdrop-filter:blur(2.5px)}.container .limit .backdrop:where(:global(.dark),:global(.dark) *){background-color:rgba(0,0,0,.1019607843)}.container .limit .navigate:hover+.backdrop{visibility:visible;opacity:1}.divider{@apply h-[1px] w-[40px] bg-[#EEEEF8]}.divider:where(:global(.dark),:global(.dark) *){@apply bg-[#242527]}.dividerMobile{@apply h-[1px] w-full bg-[#EFEFF9]}.dividerMobile:where(:global(.dark),:global(.dark) *){@apply bg-[#242527]}