.layout{@apply h-[100vh] p-[25px] pl-0 max-sm:px-[10px] max-sm:pb-[20px] max-sm:pt-0;width:calc(100% - 84px)}@media not all and (min-width: 640px){.layout{width:100%;height:calc(100% - 75px)}}.layout .container{@apply grid h-full w-full gap-x-[20px];grid-template-columns:320px 1fr;grid-template-rows:auto calc(100vh - 50px);grid-template-areas:"currentPage topPanel" "menuGrops books"}@media not all and (min-width: 1024px){.layout .container{grid-template-columns:260px 1fr}}@media not all and (min-width: 768px){.layout .container{grid-template-columns:180px 1fr}}@media not all and (min-width: 640px){.layout .container{grid-template-areas:"topPanel" "menuGrops" "books";grid-template-columns:1fr;grid-template-rows:auto}}