.page{@apply flex h-full items-center justify-center p-[30px]}.page .limit{@apply flex w-[388px] flex-col}.page .limit .title{@apply mb-[40px] text-center text-[25px] font-bold leading-[30px] text-theme-text max-sm:mb-[35px] max-sm:text-[20px]}.page .limit .title:where(:global(.dark),:global(.dark) *){@apply text-themeDark-text}.page .limit .backLink{@apply flex w-fit items-center justify-center gap-[5px] text-[15px]}.page .limit .backLink .link{@apply text-theme-blue}