.template{@apply flex items-center border-t border-[#EFEFF9] py-[16px] transition-colors ease-linear}.template .box{@apply flex grow items-center gap-[50px] max-md:gap-[20px]}.template .box .link{@apply block h-full grow cursor-pointer truncate text-[16px] font-normal text-[#6A6A6A] max-sm:text-[13px];max-width:clamp(8.125rem,-30.911rem + 81.33vw,25rem)}.template .box .link:hover{@apply text-[#4671b0]}.template:where(:global(.dark),:global(.dark) *){@apply border-[#242527]}