.container{@apply flex h-[32px] items-center rounded bg-[#e6f7ff] transition-shadow}.container .input{@apply w-full bg-[#e6f7ff] pl-[12px] text-[14px] font-medium text-[#16171a] outline-none}.container .clearButton{@apply flex h-full items-center gap-[3px] pl-[5px] pr-[12px] text-center}.container .clearButton .icon{@apply w-[14px] cursor-pointer opacity-60 transition-opacity ease-linear}.container .clearButton .icon:hover{@apply opacity-100}.container:focus{@apply shadow-[0_0_0_2px_rgba(24,144,255,.2)]}