.variation{@apply flex items-center border-t border-[#EFEFF9] py-[15px] transition-colors ease-linear}.variation .box{@apply flex grow items-center gap-[15px] max-md:gap-[10px]}.variation .box .value{@apply block h-full grow truncate text-[16px] font-medium text-[#171717] max-sm:text-[13px];max-width:clamp(6.25rem,-13.988rem + 47.62vw,18.75rem)}.variation .box .value:where(:global(.dark),:global(.dark) *){@apply text-[#A3ABB6]}@media not all and (min-width: 640px){.variation .box .value{max-width:clamp(11.25rem,-8.75rem + 100vw,31.25rem)}}.variation:where(:global(.dark),:global(.dark) *){@apply border-[#242527]}.variation:first-child{@apply border-none}